.inpBtn {
  background: #6EE6C7;
  border: 1px solid #428271;
  box-sizing: border-box;
  border-radius: 0.7vw;

  height: 12%;
  width: 40%;
  
  font-weight: 300;
  font-size: 1.1vw;
  line-height: 1.3vw;

  color: white;

  cursor: pointer;
  margin-top: 15px;
  margin-bottom: 15px;
}

.inpBtn:hover {
  background: #54b49c;
  border: 1px solid #2f5e51;
}

.inpBtn:active {
  outline: none;
}

.inpBtn:focus {
  outline: none;
  background-color: #43917d;
  border: 1px solid #204137;
}