.inpLabel {
  width: 85%;
  height: 11.5%;

  background: #FFFFFF;
  border: 1px solid #C1C1C1;
  box-sizing: border-box;
  border-radius: 0.7vw;

  font-weight: 300;
  font-size: 0.9vw;
  line-height: 1.3vw;
  padding: 15px;
  margin-top: 5px;
  margin-bottom: 5px;
}

.inpLabel:focus {
  outline: none;
}

.inpLabel::placeholder {
  color: #D3D3D3;
}