.logIn-container {
  width: 100%;
  height: 100vh;

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.logIn-logo {
  margin-bottom: 15px;
  filter: drop-shadow(0px 4px 0px rgba(0, 0, 0, 0.25));
  border-radius:  1.15vw;

  width: 44%;
}

.logIn-widget {
  width: 20%;
  height: 52vh;

  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;

  border-radius: 13px;
  background-color: #F7F7F7;
  padding: 20px;
}

.logIn-header {
  margin-bottom: 1.5vh;
  font-weight: 300;
  font-size: 1.85vw;
  line-height: 44px;
}

.logIn-footnote {
  text-align: center;
  font-weight: 300;
  font-size: 18px;
  line-height: 22px;
  line-height: 22px;
  color: #6C6C6C;
}

.logIn-footnote > a {
  color:#6C6C6C ;
  text-decoration: underline;
  cursor: pointer;
}

.logIn-footnote > a:hover {
  color: #2e2e2e;
}